.testimonials {
  &__item {
    margin-bottom: 48px;

    &:last-child {
      margin-bottom: 0;
    }

    @include onDesktop {
      margin-bottom: 0;
    }
  }
}

.testimonial {
  &__photo-container {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    /*column-gap: 24px;

    margin-bottom: 28px;*/
  }

  &__photo {
    grid-column: 2 / 4;
    width: 100%;

    transition-duration: 0.3s;
    transition-property: transform;

    @include onDesktop {
      margin-bottom: 32px;
    }

    &:hover {
      transform: scale(1.04);
    }
  }

  &__name {
    margin: 0 0 4px;

    font-weight: bold;
    font-size: 24px;
    line-height: 30px;
    text-align: center;
    color: $colorBlue;
  }

  &__role {
    margin: 0 0 4px;

    font-weight: 400;
    font-size: 14px;
    line-height: 27px;
    text-align: center;
    color: #9fadc2;
  }

  &__about {
    text-align: center;
  }
}

.link {
  text-decoration: none;
  color: $colorBlue;
}
